import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { 
  Typography, 
  Box, 
  Button, 
  Paper, 
  Table, 
  TableBody, 
  TableCell, 
  TableContainer, 
  TableHead, 
  TableRow,
  TablePagination,
  Chip,
  TextField,
  InputAdornment,
  IconButton,
  Grid,
  Alert,
  CircularProgress,
  Stack
} from '@mui/material';
import { 
  Add as AddIcon, 
  Search as SearchIcon,
  Visibility as ViewIcon,
  Edit as EditIcon,
  Delete as DeleteIcon,
  PictureAsPdf as PdfIcon,
} from '@mui/icons-material';
import { orderService, clientService, productService } from '../services/api';
import FormDialog, { ConfirmationDialog } from '../components/FormDialog';

// Componente para criar pedido
const OrderForm = ({ 
  formData, 
  setFormData, 
  clients, 
  products, 
  clientsLoading, 
  productsLoading 
}) => {
  const [items, setItems] = useState(formData.items || [{ produto: '', quantidade: 1, precoUnitario: 0 }]);

  useEffect(() => {
    setFormData(prev => ({ ...prev, items }));
  }, [items, setFormData]);

  const handleItemChange = (index, field, value) => {
    const updatedItems = [...items];
    
    // Se for alterao de produto, busca o preo
    if (field === 'produto') {
      const selectedProduct = products.find(p => p.nome === value);
      if (selectedProduct) {
        updatedItems[index] = {
          ...updatedItems[index],
          produto: value,
          precoUnitario: selectedProduct.preco || 0
        };
      } else {
        updatedItems[index][field] = value;
      }
    } else {
      updatedItems[index][field] = value;
    }
    
    setItems(updatedItems);
  };

  const addItem = () => {
    setItems([...items, { produto: '', quantidade: 1, precoUnitario: 0 }]);
  };

  const removeItem = (index) => {
    if (items.length > 1) {
      const updatedItems = items.filter((_, i) => i !== index);
      setItems(updatedItems);
    }
  };

  const calculateTotal = () => {
    return items.reduce((sum, item) => {
      return sum + (Number(item.quantidade) * Number(item.precoUnitario));
    }, 0);
  };

  return (
    <Grid container spacing={3}>
      <Grid item xs={12}>
        <TextField
          select
          fullWidth
          label="Cliente"
          value={formData.clientId || ''}
          onChange={(e) => setFormData({ ...formData, clientId: e.target.value })}
          disabled={clientsLoading}
          required
        >
          {clients.map((client) => (
            <option key={client.id} value={client.id}>
              {client.name}
            </option>
          ))}
        </TextField>
      </Grid>
      
      <Grid item xs={12}>
        <TextField
          select
          fullWidth
          label="Status"
          value={formData.status || 'Pendente'}
          onChange={(e) => setFormData({ ...formData, status: e.target.value })}
          required
        >
          <option value="Pendente">Pendente</option>
          <option value="Em Produo">Em Produo</option>
          <option value="Concludo">Concludo</option>
          <option value="Entregue">Entregue</option>
          <option value="Cancelado">Cancelado</option>
        </TextField>
      </Grid>
      
      <Grid item xs={12}>
        <Typography variant="h6" gutterBottom>
          Itens do Pedido
        </Typography>
        
        {items.map((item, index) => (
          <Box key={index} sx={{ mb: 2, p: 2, border: '1px solid #e0e0e0', borderRadius: 1 }}>
            <Grid container spacing={2}>
              <Grid item xs={12} sm={5}>
                <TextField
                  select
                  fullWidth
                  label="Produto"
                  value={item.produto}
                  onChange={(e) => handleItemChange(index, 'produto', e.target.value)}
                  disabled={productsLoading}
                  required
                >
                  {products.map((product) => (
                    <option key={product.id} value={product.nome}>
                      {product.nome}
                    </option>
                  ))}
                </TextField>
              </Grid>
              
              <Grid item xs={6} sm={2}>
                <TextField
                  fullWidth
                  type="number"
                  label="Quantidade"
                  value={item.quantidade}
                  onChange={(e) => handleItemChange(index, 'quantidade', parseInt(e.target.value) || 1)}
                  inputProps={{ min: 1 }}
                  required
                />
              </Grid>
              
              <Grid item xs={6} sm={3}>
                <TextField
                  fullWidth
                  type="number"
                  label="Preo Unitrio"
                  value={item.precoUnitario}
                  onChange={(e) => handleItemChange(index, 'precoUnitario', parseFloat(e.target.value) || 0)}
                  inputProps={{ min: 0, step: 0.01 }}
                  required
                />
              </Grid>
              
              <Grid item xs={12} sm={2} sx={{ display: 'flex', alignItems: 'center' }}>
                <Button 
                  color="error" 
                  onClick={() => removeItem(index)}
                  disabled={items.length <= 1}
                >
                  Remover
                </Button>
              </Grid>
            </Grid>
          </Box>
        ))}
        
        <Box sx={{ display: 'flex', justifyContent: 'space-between', mt: 2 }}>
          <Button 
            startIcon={<AddIcon />} 
            onClick={addItem}
          >
            Adicionar Item
          </Button>
          
          <Typography variant="h6">
            Total: R$ {calculateTotal().toFixed(2)}
          </Typography>
        </Box>
      </Grid>
    </Grid>
  );
};

const Orders = () => {
  const navigate = useNavigate();
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  
  // Estado para formulrio e dialogs
  const [openOrderForm, setOpenOrderForm] = useState(false);
  const [openDeleteDialog, setOpenDeleteDialog] = useState(false);
  const [selectedOrder, setSelectedOrder] = useState(null);
  const [formData, setFormData] = useState({ status: 'Pendente', items: [] });
  
  // Estado para clientes e produtos
  const [clients, setClients] = useState([]);
  const [products, setProducts] = useState([]);
  const [clientsLoading, setClientsLoading] = useState(false);
  const [productsLoading, setProductsLoading] = useState(false);
  
  // Paginao e filtros
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [searchTerm, setSearchTerm] = useState('');
  
  // Carregar pedidos
  const fetchOrders = async () => {
    try {
      setLoading(true);
      setError(null);
      const response = await orderService.getAll();
      setOrders(response.data);
    } catch (err) {
      console.error('Erro ao carregar pedidos:', err);
      setError('No foi possvel carregar os pedidos. Tente novamente mais tarde.');
    } finally {
      setLoading(false);
    }
  };
  
  // Carregar clientes e produtos
  const fetchClientsAndProducts = async () => {
    try {
      setClientsLoading(true);
      setProductsLoading(true);
      
      const [clientsResponse, productsResponse] = await Promise.all([
        clientService.getAll(),
        productService.getAll()
      ]);
      
      setClients(clientsResponse.data);
      setProducts(productsResponse.data);
    } catch (err) {
      console.error('Erro ao carregar dados para formulrio:', err);
      setError('No foi possvel carregar clientes e produtos. Tente novamente mais tarde.');
    } finally {
      setClientsLoading(false);
      setProductsLoading(false);
    }
  };
  
  // Efeito para carregar pedidos na inicializao
  useEffect(() => {
    fetchOrders();
  }, []);
  
  // Lidar com criao de pedido
  const handleCreateOrder = async (e) => {
    e.preventDefault();
    try {
      await orderService.create(formData);
      setOpenOrderForm(false);
      fetchOrders();
      setFormData({ status: 'Pendente', items: [] });
    } catch (err) {
      console.error('Erro ao criar pedido:', err);
      setError('No foi possvel criar o pedido. Verifique os dados e tente novamente.');
    }
  };
  
  // Lidar com excluso de pedido
  const handleDeleteOrder = async () => {
    if (!selectedOrder) return;
    
    try {
      await orderService.delete(selectedOrder.id);
      setOpenDeleteDialog(false);
      setSelectedOrder(null);
      fetchOrders();
    } catch (err) {
      console.error('Erro ao excluir pedido:', err);
      setError('No foi possvel excluir o pedido. Tente novamente mais tarde.');
    }
  };
  
  // Gerar PDF do pedido
  const handleGeneratePDF = async (orderId) => {
    try {
      const response = await orderService.generatePDF(orderId);
      
      // Criar blob e abrir/baixar PDF
      const blob = new Blob([response.data], { type: 'application/pdf' });
      const url = window.URL.createObjectURL(blob);
      window.open(url);
    } catch (err) {
      console.error('Erro ao gerar PDF:', err);
      setError('No foi possvel gerar o PDF. Tente novamente mais tarde.');
    }
  };
  
  // Abrir formulrio de pedido
  const openCreateOrderForm = () => {
    fetchClientsAndProducts();
    setFormData({ status: 'Pendente', items: [{ produto: '', quantidade: 1, precoUnitario: 0 }] });
    setOpenOrderForm(true);
  };
  
  // Filtrar pedidos pelo termo de busca
  const filteredOrders = orders.filter(order => {
    return (
      (order.clientName && order.clientName.toLowerCase().includes(searchTerm.toLowerCase())) ||
      (order.id && order.id.toLowerCase().includes(searchTerm.toLowerCase())) ||
      (order.status && order.status.toLowerCase().includes(searchTerm.toLowerCase()))
    );
  });
  
  // Controles de paginao
  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };
  
  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };
  
  // Cor do chip de status
  const getStatusColor = (status) => {
    switch (status) {
      case 'Pendente': return 'warning';
      case 'Em Produo': return 'info';
      case 'Concludo': return 'success';
      case 'Entregue': return 'success';
      case 'Cancelado': return 'error';
      default: return 'default';
    }
  };

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        Gerenciamento de Pedidos
      </Typography>
      
      {error && (
        <Alert severity="error" sx={{ mb: 3 }}>
          {error}
        </Alert>
      )}
      
      <Grid container spacing={2} sx={{ mb: 3 }}>
        <Grid item xs={12} sm={6} md={8}>
          <TextField
            fullWidth
            placeholder="Buscar pedidos por cliente, id ou status..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <SearchIcon />
                </InputAdornment>
              ),
            }}
          />
        </Grid>
        <Grid item xs={12} sm={6} md={4}>
          <Button
            fullWidth
            variant="contained"
            color="primary"
            startIcon={<AddIcon />}
            onClick={openCreateOrderForm}
          >
            Novo Pedido
          </Button>
        </Grid>
      </Grid>
      
      <Paper sx={{ width: '100%', overflow: 'hidden' }}>
        {loading ? (
          <Box sx={{ display: 'flex', justifyContent: 'center', p: 3 }}>
            <CircularProgress />
          </Box>
        ) : (
          <>
            <TableContainer sx={{ maxHeight: '60vh' }}>
              <Table stickyHeader>
                <TableHead>
                  <TableRow>
                    <TableCell>ID</TableCell>
                    <TableCell>Cliente</TableCell>
                    <TableCell>Data</TableCell>
                    <TableCell>Status</TableCell>
                    <TableCell>Itens</TableCell>
                    <TableCell align="right">Valor Total</TableCell>
                    <TableCell align="center">Aes</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {filteredOrders
                    .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                    .map((order) => {
                      // Calcular valor total do pedido
                      const total = order.items.reduce((sum, item) => {
                        return sum + (item.quantidade * item.precoUnitario);
                      }, 0);
                      
                      return (
                        <TableRow hover key={order.id}>
                          <TableCell>{order.id.substring(0, 8)}...</TableCell>
                          <TableCell>{order.clientName}</TableCell>
                          <TableCell>
                            {new Date(order.dataPedido).toLocaleDateString('pt-BR')}
                          </TableCell>
                          <TableCell>
                            <Chip 
                              label={order.status} 
                              color={getStatusColor(order.status)}
                              size="small"
                            />
                          </TableCell>
                          <TableCell>{order.items.length} itens</TableCell>
                          <TableCell align="right">
                            R$ {total.toFixed(2)}
                          </TableCell>
                          <TableCell>
                            <Stack direction="row" spacing={1} justifyContent="center">
                              <IconButton 
                                size="small" 
                                color="primary"
                                onClick={() => navigate(`/orders/${order.id}`)}
                                title="Ver detalhes"
                              >
                                <ViewIcon />
                              </IconButton>
                              <IconButton 
                                size="small"
                                color="secondary"
                                onClick={() => handleGeneratePDF(order.id)}
                                title="Gerar PDF"
                              >
                                <PdfIcon />
                              </IconButton>
                              <IconButton 
                                size="small" 
                                color="error"
                                onClick={() => {
                                  setSelectedOrder(order);
                                  setOpenDeleteDialog(true);
                                }}
                                title="Excluir pedido"
                              >
                                <DeleteIcon />
                              </IconButton>
                            </Stack>
                          </TableCell>
                        </TableRow>
                      );
                    })}
                  
                  {filteredOrders.length === 0 && (
                    <TableRow>
                      <TableCell colSpan={7} align="center">
                        Nenhum pedido encontrado
                      </TableCell>
                    </TableRow>
                  )}
                </TableBody>
              </Table>
            </TableContainer>
            
            <TablePagination
              rowsPerPageOptions={[5, 10, 25]}
              component="div"
              count={filteredOrders.length}
              rowsPerPage={rowsPerPage}
              page={page}
              onPageChange={handleChangePage}
              onRowsPerPageChange={handleChangeRowsPerPage}
              labelRowsPerPage="Itens por pgina:"
              labelDisplayedRows={({ from, to, count }) => 
                `${from}-${to} de ${count}`
              }
            />
          </>
        )}
      </Paper>
      
      {/* Dilogo de criao de pedido */}
      <FormDialog
        open={openOrderForm}
        handleClose={() => setOpenOrderForm(false)}
        title="Novo Pedido"
        onSubmit={handleCreateOrder}
        submitButtonText="Criar Pedido"
      >
        <OrderForm
          formData={formData}
          setFormData={setFormData}
          clients={clients}
          products={products}
          clientsLoading={clientsLoading}
          productsLoading={productsLoading}
        />
      </FormDialog>
      
      {/* Dilogo de confirmao de excluso */}
      <ConfirmationDialog
        open={openDeleteDialog}
        handleClose={() => setOpenDeleteDialog(false)}
        title="Confirmar Excluso"
        message={`Tem certeza que deseja excluir o pedido ${selectedOrder?.id}? Esta ao no pode ser desfeita.`}
        onConfirm={handleDeleteOrder}
        confirmButtonText="Excluir"
        confirmButtonColor="error"
      />
    </Box>
  );
};

export default Orders;
